    border-right-color: #dee7f0;
}

.nb-popover.right .handles > [dir=l] {
    display: none;
}

.nb-popover.left .arrow {
    border-left-color: #91b0cc;
}

.nb-popover.left .arrow:after {
    border-left-color: #dee7f0;
}

.nb-popover.left .handles > [dir=r] {
    display: none;
}

.nb-popover.top .arrow {
    border-top-color: #91b0cc;
}

