    border-bottom-color: #dee7f0;
}

.nb-popover.bottom .handles > [dir=t] {
    display: none;
}

.nb-popover .handles {
    display: none;
    position: absolute;
    width: 100%;
    height: 100%;
}

.nb-popover .handles > * {
    position: absolute;
}

.nb-popover .handles > *[dir=t],
.nb-popover .handles > *[dir=b] {
    left: 50%;
    margin-left: -5px;
    width: 10px;
    border-top: 1px solid #91b0cc;
    cursor: ns-resize;
}

.nb-popover .handles > *[dir=l],
.nb-popover .handles > *[dir=r] {
    top: 50%;
    margin-top: -5px;
    height: 10px;
    border-left: 1px solid #91b0cc;
    cursor: ew-resize;
}

.nb-popover .handles > *[dir=t] {
    top: 0;
}

.nb-popover .handles > *[dir=b] {
    bottom: 0;
}

.nb-popover .handles > *[dir=l] {
    left: 0;
}

.nb-popover .handles > *[dir=r] {
    right: 0;
}

.nb-popover.resizable .handles {
    display: block;
