.ui-corner-tr {
    border-top-right-radius: 4px;
}

.ui-corner-all,
.ui-corner-bottom,
.ui-corner-left,
.ui-corner-bl {
    border-bottom-left-radius: 4px;
}

.ui-corner-all,
.ui-corner-bottom,
.ui-corner-right,
.ui-corner-br {
    border-bottom-right-radius: 4px;
}

/* Overlays */
.ui-widget-overlay {
    background: #aaaaaa url(../lib/jquery-ui/images/ui-bg_flat_0_aaaaaa_40x100.png) 50% 50% repeat-x;
    opacity: .3;
}

.ui-widget-shadow {
    margin: -8px 0 0 -8px;
    padding: 8px;
    background: #aaaaaa url(../lib/jquery-ui/images/ui-bg_flat_0_aaaaaa_40x100.png) 50% 50% repeat-x;
    opacity: .3;
    border-radius: 8px;
}

.ui-select-highlight {
    font-weight: bold;
}

.ui-select-offscreen {
    clip: rect(0 0 0 0) !important;
    width: 1px !important;
    height: 1px !important;
    border: 0 !important;
    margin: 0 !important;
    padding: 0 !important;
    overflow: hidden !important;
    position: absolute !important;
    outline: 0 !important;
    left: 0 !important;
    top: 0 !important;
}

.filter-highlight {
    background: #fcc666;
    color: grey;
}
.filter-highlight-current {
    background: #fff666;
}
.ui-widget {
    font-size: 1em;
}

nb-divider,
.nb-divider {
    display: block;
    height: 0;
    border-top: 1px solid #246199;
    border-bottom: 1px solid white;
}

.ui-slider {
    background: transparent;
    border: none;
    height: 0;
}

.ui-slider:before {
    display: block;
    margin: 2px auto;
    content: " ";
    background: #444444;
    border: none;
    border-radius: 2px;
    width: 100%;
    height: 4px;
    z-index: 2;
}

.ui-slider .ui-slider-range {
    display: none;
}

.ui-slider.ui-slider-vertical {
    width: 16px;
    height: 100px;
}

.ui-slider.ui-slider-vertical .ui-slider-handle {
    left: 0;
    height: 6px;
    width: 16px;
    margin-bottom: -3px;
}

.ui-slider .ui-slider-handle {
    border: 1px solid #666666;
    background: #e6e6e6;
    z-index: 2;
    position: absolute;
    border-radius: 2px;
    cursor: pointer;
}

.ui-slider .ui-slider-handle:hover {
    background-image: -moz-linear-gradient(top, white 0%, #f2f2f2 100%);
    background-image: -webkit-linear-gradient(top, white 0%, #f2f2f2 100%);
    background-image: -o-linear-gradient(top, white 0%, #f2f2f2 100%);
    background-image: -ms-linear-gradient(top, white 0%, #f2f2f2 100%);
    background-image: linear-gradient(to bottom, white 0%, #f2f2f2 100%);
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffffff', endColorstr='#fff2f2f2', GradientType=0);
}

.ui-slider .ui-slider-handle.active,
.ui-slider .ui-slider-handle:active {
    background: #cccccc !important;
    border-color: #808080 !important;
    outline: none !important;
}

.ui-slider .ui-slider-handle:focus {
    background-image: -moz-linear-gradient(top, white 0%, #f2f2f2 100%);
    background-image: -webkit-linear-gradient(top, white 0%, #f2f2f2 100%);
    background-image: -o-linear-gradient(top, white 0%, #f2f2f2 100%);
    background-image: -ms-linear-gradient(top, white 0%, #f2f2f2 100%);
    background-image: linear-gradient(to bottom, white 0%, #f2f2f2 100%);
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffffff', endColorstr='#fff2f2f2', GradientType=0);
    outline: #7ca0c2;
}

.nb-dropdown-tree-directive.treeview-container {
    overflow: auto;
    max-height: 300px;
    padding: 2px 5px 0 5px;
    z-index: 1100;
}

.nb-dropdown-tree-directive {
    .tree-select-title {
        position: relative;
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    button {
        max-width: none;
    }
}

.session-expired-message{
    z-index: 9999 !important;
}
